<template>
  <div class="orders">
      <van-nav-bar
      title="我的订单"
      left-text="返回"
      left-arrow
      @click-left="$router.go(-1)"
      />
      <van-dropdown-menu>
        <van-dropdown-item v-model="dropdownMenu.value" :options="dropdownMenu.options" />
      </van-dropdown-menu>
      <div v-for="item in allOrders" :key="item.order_no" @click="handleClickOrder(item.order_id)">
        <orderDisplay :order="item"/>
      </div>
  </div>
</template>

<script>
import orderDisplay from '@/components/orderDisplay.vue'
import { getAllOrderListApi, getAllOrderCountApi } from '@/api/order'
export default {
  name: 'orders-index',
  components: {
    orderDisplay
  },
  data () {
    return {
      dropdownMenu: {
        value: 0,
        options: [
          { text: '全部订单', value: 0 },
          { text: '已支付', value: 1 },
          { text: '待收货', value: 2 },
          { text: '已完成', value: 3 }
        ]
      },
      allOrders: {},
      allOrdersCount: {}
    }
  },
  async created () {
    const { data: { counts } } = await getAllOrderCountApi()
    this.allOrdersCount = counts
    const { data: { list: { data: all } } } = await getAllOrderListApi('all', '1')
    this.allOrders = all
    // console.log('all orders', all)
  },
  methods: {
    handleClickOrder (orderId) {
      // console.log('clickOrder:', orderId)
      this.$router.push(`/orderDetail?order_id=${orderId}`)
    }
  }
}
</script>

