import request from '@/utils/request'
export function submitOrderInCartApi (delivery, couponId, isUsePoints, mode, payType, remark, cartIds) {
  return request.post('/checkout/submit', {
    delivery, couponId, isUsePoints, mode, payType, remark, cartIds
  })
}
export function submitOrderBuyNow (delivery, couponId, isUsePoints, mode, payType, remark, goodsId, goodsNum, goodsSkuId) {
  return request.post('/checkout/submit', {
    delivery, couponId, isUsePoints, mode, payType, remark, goodsId, goodsNum, goodsSkuId
  })
}
export function getAllOrderCountApi () {
  return request.get('/order/todoCounts')
}
export function getAllOrderListApi (dataType, page) {
  return request.get('/order/list', {
    params: {
      dataType, page
    }
  })
}
export function getOrderDetailApi (orderId) {
  return request.get('/order/detail',
    {
      params: {
        orderId
      }
    }
  )
}
export function cancleOrderApi (orderId) {
  return request.post('/order/cancel', {
    orderId
  })
}
export function payOrderApi (order) {
  return request.get('/order/pay', {
    order
  })
}
export function confirmReceiveOrderApi (orderId) {
  return request.post('/order/receipt', {
    orderId
  })
}
export function getReviewListApi () {
  return request.get('/order.comment/list')
}
